@import '../../scss/colors';

.page__calendar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.calendar__plus-button {
}

.mood__list {
  width: 290px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  list-style: none;
  margin: 0;
  padding: 0;
}
.mood__item {
}
.mood__link {
  text-decoration: none;
  color: $color-black;
}
